    resetResultBox1() {
      this.quantityOne = 0;
      this.priceOne = 0;
      this.boxText = "";
      this.activeBox = false;
      this.showResultBox1 = false;
    },
    resetResultBox2() {
      this.quantityTow = 0;
      this.priceTow = 0;
      this.boxText = "";
      this.activeBox = false;
      this.showResultBox2 = false;
    },
    resetResultBox3() {
      this.quantityThree = 0;
      this.priceThree = 0;
      this.boxText = "";
      this.activeBox = false;
      this.showResultBox3 = false;
    },
    resetResultBox4() {
      this.quantityFour = 0;
      this.priceFour = 0;
      this.boxText = "";
      this.activeBox = false;
      this.showResultBox4 = false;
    },
    resetResultBox5() {
      this.quantityFive = 0;
      this.priceFive = 0;
      this.boxText = "";
      this.activeBox = false;
      this.showResultBox5 = false;
    },
    showBox(boxNumber) {
      this.activeBox = boxNumber;
      switch (boxNumber) {
        case 1:
          this.showResultBox1 = true;
          this.boxText = " : Honeysucle";
          break;
        case 2:
          this.showResultBox2 = true;
          this.boxText = " : TURQUOISE";
          break;
        case 3:
          this.showResultBox3 = true;
          this.boxText = " : Mimosa";
          break;
        case 4:
          this.showResultBox4 = true;
          this.boxText = " : Blue Izis";
          break;
        case 5:
          this.showResultBox5 = true;
          this.boxText = " : Chili Pepper";
          break;
      }
    },
    incrementQuantity1() {
      this.quantityOne++;
      this.priceOne = (this.quantityOne * 280000).toLocaleString() + " 원";
    },
    decrementQuantity1() {
      if (this.quantityOne > 0) {
        this.quantityOne--;
        this.priceOne = this.quantityOne * 280000 + " 원";
      }
    },
    incrementQuantity2() {
      this.quantityTow++;
      this.priceTow = (this.quantityTow * 280000).toLocaleString() + " 원";
    },
    decrementQuantity2() {
      if (this.quantityTow > 0) {
        this.quantityTow--;
        this.priceTow = this.quantityTow * 280000 + " 원";
      }
    },
    incrementQuantity3() {
      this.quantityThree++;
      this.priceThree = (this.quantityThree * 280000).toLocaleString() + " 원";
    },
    decrementQuantity3() {
      if (this.quantityThree > 0) {
        this.quantityThree--;
        this.priceThree = this.quantityThree * 280000 + " 원";
      }
    },
    incrementQuantity4() {
      this.quantityFour++;
      this.priceFour = (this.quantityFour * 280000).toLocaleString() + " 원";
    },
    decrementQuantity4() {
